  $icon-arrow-down: 0px 64px 0px -64px 7px 4px 79px 68px 'sprite32be.png';
  $icon-arrow-up: 46px 45px -46px -45px 7px 4px 79px 68px 'sprite32be.png';
  $icon-cross: 20px 45px -20px -45px 11px 11px 79px 68px 'sprite32be.png';
  $icon-favorite-hover: 40px 24px -40px -24px 16px 15px 79px 68px 'sprite32be.png';
  $icon-favorite: 0px 45px 0px -45px 16px 15px 79px 68px 'sprite32be.png';
  $icon-pointer: 47px 0px -47px 0px 14px 20px 79px 68px 'sprite32be.png';
  $icon-profile-hover: 65px 21px -65px -21px 14px 17px 79px 68px 'sprite32be.png';
  $icon-profile: 65px 0px -65px 0px 14px 17px 79px 68px 'sprite32be.png';
  $icon-ruler: 0px 0px 0px 0px 20px 20px 79px 68px 'sprite32be.png';
  $icon-search-hover: 20px 24px -20px -24px 16px 17px 79px 68px 'sprite32be.png';
  $icon-search: 0px 24px 0px -24px 16px 17px 79px 68px 'sprite32be.png';
  $icon-shopping-bag-hover: 65px 42px -65px -42px 13px 18px 79px 68px 'sprite32be.png';
  $icon-shopping-bag: 35px 45px -35px -45px 7px 9px 79px 68px 'sprite32be.png';
  $icon-sort-default: 24px 0px -24px 0px 19px 19px 79px 68px 'sprite32be.png';

  @mixin sprite-position($sprite) {
    $sprite-offset-x: nth($sprite, 3);
    $sprite-offset-y: nth($sprite, 4);
    background-position: $sprite-offset-x  $sprite-offset-y;
  }

  @mixin sprite-image($sprite) {
    $sprite-image: nth($sprite, 9);
    background-image: url(%=static=%png-sprite/96dpi/#{$sprite-image});
  }

  @mixin sprite-mixin-template($sprite) {
    @include sprite-image($sprite);
    @include sprite-position($sprite);
    width: nth($sprite, 5);
    height: nth($sprite, 6);
  }

  @mixin bg($sprite, $repeat: no-repeat) {
    @include sprite-mixin-template($sprite);
    $sprite-total-width: nth($sprite, 7);
    $sprite-total-height: nth($sprite, 8);
    background-size: $sprite-total-width, $sprite-total-height;
    background-repeat: $repeat;



  }
